/* eslint-disable no-eval */
export const createElementByText = (textHTML = "") => {
    const template = document.createElement("template");
    textHTML = textHTML.trim(); // Never return a text node of whitespace as the result
    template.innerHTML = textHTML;
    return template.content.firstElementChild;
};
export function copyToClipboard(text) {
    try {
        const elem = document.createElement("textarea");
        elem.value = text;
        document.body.appendChild(elem);
        elem.select();
        document.execCommand("copy");
        document.body.removeChild(elem);
        return true;
    }
    catch (error) {
        return false;
    }
}
export const isEmptyString = (str) => {
    return str === "" || str === null || str === undefined;
};
export function globalVarIsExist(varName) {
    try {
        return !!window?.[varName] ?? eval(`typeof ${varName}`) !== "undefined";
    }
    catch (error) {
        // console.log('varIsExist', error);
        return false;
    }
}
export function getGlobalVar(varName) {
    try {
        return globalVarIsExist(varName) ? window?.[varName] ?? eval(varName) : undefined;
    }
    catch (error) {
        return undefined;
    }
}
export function getChrome() {
    return getGlobalVar("chrome");
}
export async function getCurrentTab() {
    return (await getChrome()?.tabs?.query?.({
        active: true,
        currentWindow: true,
    }))?.[0];
}
export function tryEval(str, context) {
    try {
        const contextString = `
                const { ${Object.keys(context ?? {}).join(",")} } = context ?? {};
            `;
        return eval(contextString + str);
    }
    catch (error) {
        console.error("tryEval =>", error);
        return undefined;
    }
}
export function getRuntimeEnvironment() {
    const envs = [];
    try {
        const chrome = getChrome();
        if (!chrome) {
            envs.push("browser");
        }
        if (typeof chrome?.extension !== "undefined") {
            console.log("Script ang chy trong background.");
            envs.push("background");
        }
        else if (typeof chrome?.runtime !== "undefined") {
            console.log("Script ang chy trong background hoc event page.");
            envs.push("background_or_event_page");
        }
        const popupExists = chrome?.extension?.getViews?.({ type: "popup" })?.length > 0;
        if (popupExists) {
            console.log("Script ang chy trong popup.");
            envs.push("popup");
        }
        const isContentScript = window?.location?.href?.startsWith?.("http");
        const backgroundPageExists = typeof chrome?.extension?.getBackgroundPage?.() !== "undefined";
        if (isContentScript) {
            console.log("Script ang chy trong content script.");
            envs.push("content_script");
        }
        if (backgroundPageExists) {
            console.log("Script ang chy trong content script v background.");
            envs.push("content_script_and_background");
        }
        return envs;
    }
    catch (error) {
        console.log("getRuntimeEnvironment", error);
        envs.push("unknown");
        return envs;
    }
}
export const parseJson = (value) => {
    try {
        return JSON.parse(typeof value === "string" ? value : JSON.stringify(value));
    }
    catch (error) {
        console.log("Error in parseJson", error);
        return undefined;
    }
};
export const isMatchWebsite = (matchRegexUrls) => {
    return matchRegexUrls?.some((url) => new RegExp(url).test(window.location.href));
};
export const injectReplaceCSS = (() => {
    const style = document.createElement("style");
    document.head.append(style);
    return (cssText) => {
        try {
            style.textContent = cssText;
        }
        catch (error) {
            console.log(`Error in injectReplaceCSS ${error}`);
        }
    };
})();
export const waitUntil = async (conditionCb, interval = 100, maxAttempts = 5000) => {
    let attempts = 0;
    // eslint-disable-next-line no-async-promise-executor
    return new Promise(async (resolve, reject) => {
        try {
            if (await conditionCb()) {
                return resolve(null);
            }
            const intervalId = setInterval(async function () {
                attempts++;
                try {
                    const a = await conditionCb();
                    if (a || attempts >= maxAttempts) {
                        resolve(null);
                        clearInterval(intervalId);
                        return null;
                    }
                }
                catch (error) {
                    reject(error);
                    clearInterval(intervalId);
                }
            }, interval);
        }
        catch (error) {
            console.log("Error in waitUntil", error);
            reject(error);
        }
    });
};
export const delay = (ts) => {
    return new Promise((resolve) => {
        setTimeout(() => {
            resolve(null);
        }, ts);
    });
};
export function addScript(src) {
    const s = document.createElement("script");
    s.setAttribute("src", src);
    document.body.appendChild(s);
}
export function getManifestVersion() {
    return chrome.runtime.getManifest().manifest_version;
}
const HELPER = {
    createElementByText,
    copyToClipboard,
    isEmptyString,
    globalVarIsExist,
    getGlobalVar,
    getChrome,
    getCurrentTab,
    tryEval,
    getRuntimeEnvironment,
    parseJson,
    isMatchWebsite,
    injectReplaceCSS,
    waitUntil,
    delay,
    addScript,
};
export default HELPER;
